"use client"

import type React from "react"
import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Leaf, Store, Sprout } from "lucide-react"
import { DEMO_USERS, saveUser } from "@/lib/auth"

export default function LoginPage() {
  const router = useRouter()
  const [selectedRole, setSelectedRole] = useState<"restaurateur" | "agriculteur" | null>(null)
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(false)

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setLoading(true)

    try {
      if (!email || !password) {
        setError("Veuillez remplir tous les champs")
        return
      }

      const demoUser = DEMO_USERS[email]
      if (!demoUser || demoUser.password !== password) {
        setError("Email ou mot de passe incorrect")
        return
      }

      if (demoUser.user.role !== selectedRole) {
        setError(`Cet email est associé à un compte ${demoUser.user.role}, pas ${selectedRole}`)
        return
      }

      saveUser(demoUser.user)

      const redirectUrl = demoUser.user.role === "restaurateur" ? "/products" : "/farmer/products"
      router.push(redirectUrl)
    } catch (err) {
      setError("Erreur de connexion")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-emerald-50 flex flex-col items-center justify-center px-4">
      <Link href="/" className="mb-8 flex items-center gap-2">
        <Leaf className="w-8 h-8 text-green-600" />
        <span className="text-2xl font-bold text-green-700">AgroMarket</span>
      </Link>

      {!selectedRole ? (
        <Card className="w-full max-w-2xl p-8 shadow-lg">
          <h1 className="text-3xl font-bold text-center mb-2 text-gray-900">Qui êtes-vous?</h1>
          <p className="text-center text-gray-600 mb-8">Sélectionnez votre profil pour continuer</p>

          <div className="grid md:grid-cols-2 gap-6">
            {/* Restaurateur button */}
            <button
              onClick={() => setSelectedRole("restaurateur")}
              className="p-6 border-2 border-gray-200 rounded-lg hover:border-blue-500 hover:bg-blue-50 transition-all"
            >
              <Store className="w-12 h-12 text-blue-600 mx-auto mb-4" />
              <h2 className="text-xl font-bold text-gray-900 mb-2">Restaurateur</h2>
              <p className="text-gray-600">Accédez au catalogue de produits et gérez vos commandes</p>
            </button>

            {/* Agriculteur button */}
            <button
              onClick={() => setSelectedRole("agriculteur")}
              className="p-6 border-2 border-gray-200 rounded-lg hover:border-green-500 hover:bg-green-50 transition-all"
            >
              <Sprout className="w-12 h-12 text-green-600 mx-auto mb-4" />
              <h2 className="text-xl font-bold text-gray-900 mb-2">Agriculteur</h2>
              <p className="text-gray-600">Gérez vos produits et suivez vos récoltes</p>
            </button>
          </div>

          <div className="mt-6 text-center">
            <Link href="/">
              <Button variant="ghost" size="sm">
                Retour à l'accueil
              </Button>
            </Link>
          </div>
        </Card>
      ) : (
        <Card className="w-full max-w-md p-8 shadow-lg">
          <div className="flex items-center justify-between mb-6">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Connexion</h1>
              <p className="text-sm text-gray-600">
                {selectedRole === "restaurateur" ? "Restaurateur" : "Agriculteur"}
              </p>
            </div>
            <button
              onClick={() => {
                setSelectedRole(null)
                setEmail("")
                setPassword("")
                setError("")
              }}
              className="text-gray-400 hover:text-gray-600"
            >
              ✕
            </button>
          </div>

          <form onSubmit={handleLogin} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Email</label>
              <Input
                type="email"
                placeholder="votre@email.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Mot de passe</label>
              <Input
                type="password"
                placeholder="••••••••"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full"
              />
            </div>

            {error && <div className="p-3 bg-red-50 border border-red-200 rounded text-red-700 text-sm">{error}</div>}

            <Button
              type="submit"
              disabled={loading}
              className={`w-full text-white py-2 ${
                selectedRole === "restaurateur" ? "bg-blue-600 hover:bg-blue-700" : "bg-green-600 hover:bg-green-700"
              }`}
            >
              {loading ? "Connexion..." : "Se connecter"}
            </Button>
          </form>

          <div className="mt-6 pt-6 border-t border-gray-200">
            <p className="text-center text-sm font-semibold text-gray-700 mb-3">Comptes de démonstration:</p>
            <div className="space-y-3 text-xs">
              {selectedRole === "restaurateur" ? (
                <div className="p-3 bg-blue-50 rounded border border-blue-200">
                  <p className="font-semibold text-blue-900">Restaurateur</p>
                  <p className="font-mono text-blue-700">restaurateur@demo.com</p>
                  <p className="font-mono text-blue-700">demo123</p>
                </div>
              ) : (
                <div className="p-3 bg-green-50 rounded border border-green-200">
                  <p className="font-semibold text-green-900">Agriculteur</p>
                  <p className="font-mono text-green-700">agriculteur@demo.com</p>
                  <p className="font-mono text-green-700">demo123</p>
                </div>
              )}
            </div>
          </div>
        </Card>
      )}
    </div>
  )
}
